<script>
import { mapGetters } from "vuex"
import store from '@/store/index'
import moment from 'moment'


 export default {
  components: {

  },

  methods:{

  },

  data: () => ({

          
  }),

  computed: {
   chartOptions:function(){
     const theme = localStorage.getItem("dark_theme");
     let settings ={
        noData: {
          text: 'Ожидаем данные...',
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: '#51a9f0',
            fontSize: '19px',
            fontFamily: undefined
          }
        },

        plotOptions: {
          bar: {
            columnWidth: '70%'
          },
        },

        colors:[ 'RGBA(102, 184, 250)', 'RGBA(102, 184, 250, 0.7)','RGBA(102, 184, 250, 0.4)',],
        
        tooltip: {
          theme: 'dark',
        },

        chart: {
          id:'recommendationTrend',
          stacked: true,
          toolbar: {
              show: false,
          },
          background: 'transparent'
          // sparkline: {
          //     enabled: true
          // },
        },

        grid: {
          show: false,
        },

        xaxis: {
            categories: [ moment().locale("ru").subtract(3, 'Month').format("MMMM YYYY"), moment().locale("ru").subtract(2, 'Month').format("MMMM YYYY"), moment().locale("ru").subtract(1, 'Month').format("MMMM YYYY"), moment().locale("ru").subtract(0, 'Month').format("MMMM YYYY"),],
        },

        yaxis: {
          show: false,
        },
        
        legend: {
          show: false,
        },

        states: {
            active: {
                filter: {
                    type: 'none'
                }
            }
        },
      }
      if (theme) {
        if (theme == "true") {
          settings.theme= {
              mode: 'dark', 
              monochrome: {
                  enabled: false,
                  color: '#1e2022',
                  shadeTo: 'dark',
                  shadeIntensity: 0.65
              },
            } 
          settings.tooltip= {
            theme: 'dark',
          }          
        } else {
          settings.theme= {
            mode: 'light', 
            monochrome: {
                enabled: false,
                color: '#1e2022',
                shadeTo: 'light',
                shadeIntensity: 0.65
            },
          }
          settings.tooltip= {
            theme: 'light',             
          }     
        }
        return settings
      }
   },

